.servicesWrapper {
  padding: 40px 0;
  .headerContent {
    margin: 0 auto;
  }
  .servicesBlock {
    // display: flex;
    // flex-direction: column;
    // gap: 50px;
    @media (max-width: 425px) {
      gap: 30px;
      .service {
        row-gap: 30px;
      }
    }
    .serviceWrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      row-gap: 20px;
      .serviceBody {
        display: flex;
        flex-direction: column;
        border-radius: 0.5rem;
        gap: 20px;
        text-align: center;
        @media (min-width: 769px) {
          flex-direction: row;
          text-align: unset;
          .left {
            border-right: 1px solid #000;
            padding-right: 1.5rem;
          }

          .right {
            padding-left: 1.5rem;
          }
        }

        .column {
          flex: 1;
        }
      }
    }
  }
}
